import { blockHead, BlockHead } from './lib/block-head.decorator';

console.log('Test');


@BlockHead
class Cat {
	name: any = 'default';
	
	@blockHead
	meow() {
		return `${this.name} says Meow!`
	}
}

let Betty: any = new Cat();
Betty.foo = 'Bat Metal';
Betty.name = 'Betty';
Betty.addLink();
console.log('cat', Betty.meow());
Betty.name = 'Ninja';
console.log('cat', Betty.meow());
console.log('cat', Betty.getChain());



